import { doc, getDoc } from "firebase/firestore";
import { useContext, useEffect, useState } from "react";
import { db, updateLikes } from "../firebase/config";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHeart, faComment } from '@fortawesome/free-regular-svg-icons';
import { faHeart as heartSolid,faComment as commentSolid } from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from "react-router-dom";
import { UserContext } from "../UserContext";
export default function Tweet({data}){
  const {likes, replies, tweet, user, timestamp, id } = data;
  const [acc, setAcc] = useState({});
  const navigate = useNavigate();
  const currentUser = useContext(UserContext);
  useEffect(()=>{
    (async function(){
      const docSnap = await getDoc(doc(db,'users',user));
      setAcc({id:docSnap.id,...docSnap.data()})
    })()
  },[])
  function handleLike(e,id, user){
    e.stopPropagation();
    updateLikes(id,user);
  }
  function handleProfile(e){
    e.stopPropagation();
    navigate('/p/'+acc.id);
  }

  return (
    <div onClick={()=>navigate('/'+id)} className="w-full border-b p-4 border-gray-600 hover:bg-[#1C2732] cursor-pointer">
            
      <li className="flex py-4 first:pt-0 last:pb-0">
          <div onClick={(e)=> handleProfile(e)} className="shrink-0">
              <img className="h-12 w-12 object-cover rounded-full" src={acc.photoURL||`https://avatars.dicebear.com/api/initials/${acc.fullName}.svg`} alt="Current profile photo" />
          </div>
          <div className="ml-3 w-full">
              <p className="text-sm font-semibold">{acc&&acc.fullName}<span className="text-sm font-medium text-slate-400 truncate"> @{acc&&acc.username} . {timestamp}</span></p>
              <p className="mt-1 text-sm font-medium">{tweet}</p>
              <div className="flex gap-24 mt-2">
                <button onClick={()=>navigate('/'+id)} className={`-ml-2 flex w-14 items-center group text-${replies.find(x => x.user === currentUser.id)?'blue':'gray'}-400`}>
                  <div className="transition h-8 w-8 flex items-center justify-center rounded-full group-hover:bg-blue-400/20"><FontAwesomeIcon className="group-hover:text-blue-400" icon={replies.find(x => x.user === currentUser.id)?commentSolid:faComment}/></div>
                  <span className="ml-1 text-sm group-hover:text-blue-400">{replies.length||''}</span>
                </button>
                <button onClick={(e)=> handleLike(e,id,currentUser.id)} className={`flex items-center text-gray-400 group ${likes.includes(currentUser.id)&&'text-pink-700'}`}>
                  <div className="transition h-8 w-8 flex items-center justify-center rounded-full group-hover:bg-pink-700/20"><FontAwesomeIcon className="group-hover:text-pink-700" icon={likes.includes(currentUser.id)?heartSolid:faHeart}/></div>
                  <span className="ml-1 text-sm group-hover:text-pink-700">{likes.length||''}</span>
                </button>
              </div>
          </div>
      </li>
    </div>
  )
}
