import { collection, onSnapshot, orderBy, query } from "firebase/firestore";
import { useContext, useEffect, useState } from "react";
import { addTweet, db } from "../firebase/config";
import { UserContext } from "../UserContext";
import Tweet from "./Tweet"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faImage } from '@fortawesome/free-regular-svg-icons';

export default function Timeline() {
  const [tl, setTl] = useState([]);
  const [tweet, setTweet] = useState('');
  const currentUser = useContext(UserContext);
  useEffect(()=>{
    const q = query(collection(db,'timeline'),orderBy('timestamp','desc'))
    const unsub = onSnapshot(q, (querySnapshot) => {
      setTl(querySnapshot.docs)
    });
    return unsub
  },[])
  async function handleTweet(){
    await addTweet(currentUser.id,tweet);
    setTweet('');
  }
  return (
    <div className="w-full">
      <div className=" w-full border-b p-4 border-gray-600">
        <li className="flex py-4 first:pt-0 last:pb-0">
            <div className="shrink-0 mt-2">
                <img className="h-12 w-12 object-cover rounded-full" src={(currentUser&&currentUser.photoURL)||`https://avatars.dicebear.com/api/initials/${(currentUser&&currentUser.fullName)&&currentUser.fullName}.svg`} alt="Current profile photo" />
            </div>
            <div className="ml-3 overflow-hidden w-full">
              <input 
                className="w-full font-medium text-lg bg-transparent p-4 bg-gray-600 rounded-full focus-visible:outline-none"
                id="tweet" placeholder="What's happening?" value={tweet} onChange={e => setTweet(e.target.value)} type="text" />
              <div className="pt-2 w-full flex items-center pt-4">
                <div className="transition h-10 w-10 flex items-center justify-center rounded-full hover:bg-gray-600 group"><FontAwesomeIcon className="text-lg group-hover:text-blue-300 text-blue-400" icon={faImage} /></div>
                <button onClick={handleTweet} type="button" className="text-center ml-auto px-4 py-2 text-sm rounded-full transition font-semibold bg-blue-400 hover:bg-blue-500">
                  Tweet
                </button>  
              </div>
            </div>
        </li>
      </div>
      {tl.map(tweet=>{
        const data = {id:tweet.id,...tweet.data()};
        const {timestamp, ...dataWithoutTS} = data
        return <Tweet key={tweet.id} data={{...dataWithoutTS, timestamp:timestamp&&timestamp.toDate().toLocaleDateString()}} />
      })}
      <div className="h-52"></div>
    </div>
  )
}
